<template>
  <div id="responses" v-if="forms !== undefined && categories !== undefined">
    <h1>Formulários da Plataforma</h1>
    <table class="">
      <caption>
        Formulários
      </caption>
      <thead>
        <tr>
          <th>
            Formulário
          </th>
          <th>Respostas</th>
          <th>Editar</th>
          <th>Eliminar</th>
          <th>Tipo</th>
          <th>Categoria</th>
          <th>Ativo</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="form in forms"
          :key="form.id"
        >
          <td>
              {{ form.title }}
          </td>
          <td align="right">
            <a v-if="form.responses > 0" class="flex row ra width-100" :href="'responses/'+form.id">
              {{ form.responses }}
            </a>
            <template v-else>{{ form.responses }}</template>
          </td>
          <td align="right">
              <a :href="'editForm/' + form.id">Editar</a>
          </td>
          <td align="right">
              <a v-if="form.responses == 0" href="#delete" @click="remove(form.id)">Eliminar</a>
              <span v-else>Eliminar</span>
          </td>
          <td align="right">
            <select
              v-model.number="form.type"
              class="status flex row width-100"
              @change="changeType(form.id, form.type == '' ? null : form.type)"
              style="color : black;"
              :disabled="form.category && (categories[form.category].formsignupid == form.id || categories[form.category].formproposalid == form.id || categories[form.category].formfeedbackid == form.id)"
            >
              <option
                v-for="(type, key) in types"
                :key="key"
                :value="key"
                style="background-color: white; color: black;"
              >
                {{ type.name }}
              </option>
            </select>
          </td>
          <td align="right">
            <select
              v-model.number="form.category"
              class="status flex row width-100"
              @change="changeCategory(form.id, form.category == '' ? null : form.category)"
              style="color : black;"
              :disabled="form.category && (categories[form.category].formsignupid == form.id || categories[form.category].formproposalid == form.id || categories[form.category].formfeedbackid == form.id)"
            >
              <option>
              </option>
              <option
                v-for="(category, key) in categories"
                :key="key"
                :value="key"
                style="background-color: white; color: black;"
              >
                {{ category.name }}
              </option>
            </select>
          </td>
          <td>
            <label
              class="switch" style="margin-top: 7px;"
            >
              <input
                v-if="form.type==1"
                type="checkbox"
                :checked="form.category && categories[form.category].formsignupid == form.id"
                :disabled="form.category && categories[form.category].formsignupid == form.id"
                @change="changeActive(form.category, form.id, form.type)"
              >
              <input
                v-if="form.type==2"
                type="checkbox"
                :checked="form.category && categories[form.category].formproposalid == form.id"
                :disabled="form.category && categories[form.category].formproposalid == form.id"
                @change="changeActive(form.category, form.id, form.type)"
              >
              <input
                v-if="form.type==3"
                :checked="form.category && categories[form.category].formfeedbackid == form.id"
                :disabled="form.category && categories[form.category].formfeedbackid == form.id"
                type="checkbox"
                @change="changeActive(form.category, form.id, form.type)"
              >
              <span class="slider round noafter"/>
            </label>
          </td>
        </tr>
        <tr>
          <td colspan=7 align="center"><a class="flex row ca width-100" href="createForm">Criar novo formulário</a></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import * as components from "@/fields";
import { API } from "@/http-common";
export default {
  name: "CreateResponse",
  components,
  data() {
    return {
      forms: undefined,
      categories : undefined,
      types : undefined
    };
  },
  created() {
    API.get("formType/")
      .then((response) => this.types = response.data);
    API.get("form/")
      .then((response) => this.forms = response.data);
    API.get("eventType/")
      .then((response) => this.categories = response.data);
  },
  methods: {
    remove(id) {
      if(confirm('De certeza que quer eliminar este formulário?')) {
        this.forms = this.forms.filter((x) => (x.id != id));
        const options = {
          method: "delete",
          url: "/form/" + id + "/",
          headers: {
            "X-CSRFToken": this.getCookie("csrftoken"),
          },
        };
        API(options);
      }
    },
    changeCategory(answer, category) {
      const options = {
        method: "patch",
        url: "/form/" + answer + "/",
        headers: {
          "X-CSRFToken": this.getCookie("csrftoken"),
        },
        data: {
          "category": category
        },
        transformResponse: [
          (data) => {
            this.response = data;
            return data;
          },
        ],
      };
      API(options);
    },
    changeType(answer, type) {
      const options = {
        method: "patch",
        url: "/form/" + answer + "/",
        headers: {
          "X-CSRFToken": this.getCookie("csrftoken"),
        },
        data: {
          "type": type
        },
        transformResponse: [
          (data) => {
            this.response = data;
            return data;
          },
        ],
      };
      API(options);
    },
    changeActive(category, active, type) {
      var dataSend;
      if (type == 1)
        dataSend = {"formsignupid" : active};
      else if (type == 2)
        dataSend = {"formproposalid" : active};
      else if (type == 3)
        dataSend = {"formfeedbackid" : active};
      const options = {
        method: "patch",
        url: "/eventType/" + category + "/",
        headers: {
          "X-CSRFToken": this.getCookie("csrftoken"),
        },
        data: dataSend,
        transformResponse: [
          (data) => {
            this.response = data;
            return data;
          },
        ],
      };
      API(options).then((response) => {API.get("eventType/")
      .then((response) => this.categories = response.data);});
    },
    getCookie(name) {
      let cookieValue = null;
      if (document.cookie && document.cookie !== "") {
        const cookies = document.cookie.split(";");
        for (let i = 0; i < cookies.length; i++) {
          const cookie = cookies[i].trim();
          // Does this cookie string begin with the name we want?
          if (cookie.substring(0, name.length + 1) === name + "=") {
            cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
            break;
          }
        }
      }
      return cookieValue;
    },
  },
};
</script>

<style lang="scss">
#form-area {
  width: 100%;
}
.no-bottom {
  padding-bottom: 0;
}

.invalid {
  background-color: #e99;
  padding: 5px;
}

.requiredticker {
  color: #b11;
}

.noafter:after {
  content: '' !important; 
}
</style>
